* {
  box-sizing: border-box;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
body,
html {
  margin: 0;
  padding: 0;
}
.broadcast.error {
  font-size: 12px;
  transition: all 0.2s ease-in-out;
  padding: 8px;
  margin-top: 6px;
  margin-bottom: 16px;
  border: none;
  border-radius: 2px;
  text-align: center;
  background: #fde1df;
  color: #F44336;
  overflow: hidden;
}
body,
html {
  margin: 0;
  overflow: hidden;
}
#dice-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  pointer-events: none;
  perspective-origin: center center;
  perspective: 700px;
}
.login-wrapper form .mui-btn {
  width: 120px;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  z-index: 1000;
  overflow: auto;
  perspective-origin: center center;
  perspective: 700px;
  opacity: 1;
  transition: all 0.5s ease;
}
.overlay .popin {
  max-width: 400px;
  padding: 50px;
  transition: all 0.7s ease;
  transform: translate3d(0px, 0px, 0px);
  perspective-origin: center center;
  perspective: 500px;
}
.overlay .image {
  text-align: center;
  padding: 20px;
}
.overlay .image img {
  vertical-align: middle;
  max-width: 100%;
  max-height: 100%;
}
.overlay h3 {
  margin-top: 0;
}
.before.overlay {
  opacity: 0;
}
.before.overlay .popin {
  transform: translate3d(0px, 0px, 1000px);
}
.link {
  cursor: pointer;
}
.link:hover {
  color: #2196F3;
}
button.fa {
  background: none;
  padding: 0;
  border: none;
  outline: 0;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 12px 10px;
  color: #fff;
  background: #111;
  z-index: 1;
}
header h2 {
  margin: 0;
}
header .fa {
  font-size: 20px;
  padding: 8px;
}
#logout {
  cursor: pointer;
  color: #999;
}
#logout:hover,
#logout:focus {
  color: #fff;
}
section.middle {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 60px 0;
}
section.middle aside {
  width: 240px;
  height: 100%;
  float: left;
}
#options {
  display: none;
}
#content {
  position: relative;
  height: 100%;
  margin-left: 240px;
  margin-right: 60px;
  overflow-y: scroll;
  padding: 20px;
}
#users {
  height: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  background: #e5e5e5;
}
.user-item {
  position: relative;
  padding: 15px 10px;
  border-bottom: 1px #f5f5f5 solid;
  height: 51px;
  cursor: pointer;
  background: #e5e5e5;
  list-style: none;
}
.user-item.admin {
  color: #2196F3;
}
.user-item.admin:hover .user-details {
  display: none;
}
.user-item.me {
  background: #f5f5f5;
}
.user-item.typing:after {
  content: "\f27b";
  display: inline-block;
  margin-left: 5px;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.user-item:hover .user-details {
  display: block;
}
.user-dice {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  padding: 10px;
  text-align: center;
}
.user-dice .dice-original {
  display: block;
  font-size: 10px;
  line-height: 14px;
}
.user-dice .dice-total {
  display: block;
  font-size: 16px;
  line-height: 16px;
  font-weight: bold;
  color: inherit;
}
.user-dice .dice-detail {
  display: none;
}
.user-details {
  display: none;
  position: absolute;
  left: 100%;
  top: 0;
  z-index: 10000;
  color: #111;
  padding: 10px 5px 5px 10px;
  width: 200px;
  cursor: default;
}
.user-details .title {
  margin-bottom: 5px;
}
.user-details .fa-heart {
  color: #b50a00;
  font-size: 12px;
  text-shadow: 0 0 8px rgba(181, 10, 0, 0.3);
}
.user-details .fa-shield {
  color: #666;
  font-size: 14px;
  text-shadow: 0 0 8px rgba(0, 0, 0, 0.15);
}
.user-details:after,
.user-details:before {
  top: 15px;
  left: 0;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.user-details:after {
  border-width: 10px 10px 10px 0;
  border-color: transparent #fff transparent transparent;
  margin-left: -10px;
}
.user-details:before {
  border-width: 11px 12px 11px 0;
  border-color: transparent rgba(0, 0, 0, 0.1) transparent transparent;
  margin-left: -12px;
}
.token {
  position: relative;
  display: inline-block;
  width: 30px;
  margin: 0 5px 5px 0;
  line-height: 30px;
  font-size: 12px;
  font-weight: bold;
  border-radius: 9999px;
  text-align: center;
  transition: all 1s ease;
}
.token:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 1px;
  width: 30px;
  height: 32px;
  border-radius: 9999px;
  z-index: -1;
}
.token.navy {
  color: #ffffff;
  background: #001F3F;
}
.token.navy:after {
  background: #001932;
}
.token.blue {
  color: #ffffff;
  background: #0074D9;
}
.token.blue:after {
  background: #005dae;
}
.token.aqua {
  color: #111111;
  background: #7FDBFF;
}
.token.aqua:after {
  background: #66afcc;
}
.token.teal {
  color: #111111;
  background: #39CCCC;
}
.token.teal:after {
  background: #2ea3a3;
}
.token.olive {
  color: #ffffff;
  background: #3D9970;
}
.token.olive:after {
  background: #317a5a;
}
.token.green {
  color: #111111;
  background: #2ECC40;
}
.token.green:after {
  background: #25a333;
}
.token.lime {
  color: #111111;
  background: #01FF70;
}
.token.lime:after {
  background: #01cc5a;
}
.token.yellow {
  color: #111111;
  background: #FFDC00;
}
.token.yellow:after {
  background: #ccb000;
}
.token.orange {
  color: #ffffff;
  background: #FF851B;
}
.token.orange:after {
  background: #cc6a16;
}
.token.red {
  color: #ffffff;
  background: #FF4136;
}
.token.red:after {
  background: #cc342b;
}
.token.maroon {
  color: #ffffff;
  background: #85144B;
}
.token.maroon:after {
  background: #6a103c;
}
.token.fuchsia {
  color: #ffffff;
  background: #F012BE;
}
.token.fuchsia:after {
  background: #c00e98;
}
.token.purple {
  color: #ffffff;
  background: #B10DC9;
}
.token.purple:after {
  background: #8e0aa1;
}
.token.silver {
  color: #111111;
  background: #DDDDDD;
}
.token.silver:after {
  background: #b1b1b1;
}
.token.grey {
  color: #ffffff;
  background: #AAAAAA;
}
.token.grey:after {
  background: #888888;
}
.token.black {
  color: #ffffff;
  background: #111111;
}
.token.black:after {
  background: #0e0e0e;
}
.token.big {
  width: 120px;
  line-height: 120px;
  margin: 0;
  transform: translate3d(0px, 0px, 30px) rotateZ(15deg) rotateY(15deg);
}
.token.big:after {
  content: none;
}
.before .token {
  transform: translate3d(0px, 0px, 100px) rotateZ(-30deg) rotateY(180deg);
}
.notoken {
  line-height: 18px;
  font-size: 12px;
  background: #f0f0f0;
  padding: 6px 8px;
  border-radius: 3px;
  margin: 0 5px 5px 0;
}
[data-carac] {
  line-height: 18px;
  font-size: 18px;
  background: #f0f0f0;
  padding: 6px 8px;
  display: inline-block;
  border-radius: 3px;
  margin: 0 5px 5px 0;
}
[data-carac] span,
[data-carac] i {
  vertical-align: middle;
}
.me .user-details .token,
.admin-layout .user-details .token {
  cursor: pointer;
}
.me .user-details [data-carac],
.admin-layout .user-details [data-carac] {
  cursor: ew-resize;
}
footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #f5f5f5;
  z-index: 1;
}
footer #chat {
  padding: 14px 20px;
}
footer #chat .left-message {
  display: block;
  margin-right: 120px;
}
footer #chat input {
  display: block;
  width: 100%;
  padding: 10px;
}
footer #chat button {
  float: right;
  margin: 0;
  width: 100px;
}
#messages {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
#messages .message {
  position: relative;
  padding: 10px 15px;
  margin: 16px 40% 16px 0;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
#messages .message.me {
  margin-right: 0;
  margin-left: 40%;
  background: #f5f5f5;
}
#messages .message.me:after,
#messages .message.me:before {
  left: 90%;
}
#messages .message.me:after {
  border-color: rgba(245, 245, 245, 0);
  border-top-color: #f5f5f5;
}
#messages .message.admin {
  color: #fff;
  background: #2196F3;
}
#messages .message.admin a {
  color: #fff;
}
#messages .message.admin:after {
  border-color: rgba(245, 245, 245, 0);
  border-top-color: #2196F3;
}
#messages .message.error {
  color: #F44336;
  background: #fde1df;
}
#messages .message.error:after,
#messages .message.error:before {
  content: none;
}
#messages .message.warning {
  color: #ccb000;
  background: #fff8cc;
}
#messages .message.warning:after,
#messages .message.warning:before {
  content: none;
}
#messages .message.prive {
  opacity: 0.5;
}
#messages .message.prive:hover {
  opacity: 1;
}
#messages .message .user {
  display: block;
  font-weight: bold;
}
#messages .message img {
  max-width: 100%;
  margin: 10px 0;
}
#messages .message:after,
#messages .message:before {
  top: 100%;
  left: 10%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
#messages .message:after {
  border-color: rgba(255, 255, 255, 0);
  border-top-color: #ffffff;
  border-width: 10px;
  margin-left: -10px;
}
#messages .message:before {
  border-color: rgba(225, 225, 225, 0);
  border-top-color: #E1E1E1;
  border-width: 13px;
  margin-left: -13px;
}
#messages .diceroll {
  margin: 24px auto;
  width: 180px;
  padding: 16px;
  text-align: center;
}
#messages .diceroll.me {
  background: #f5f5f5;
}
#messages .diceroll.admin {
  background: #2196F3;
}
#messages .diceroll.admin .user,
#messages .diceroll.admin .dice-original,
#messages .diceroll.admin .dice-total {
  color: #fff;
}
#messages .diceroll.admin .dice-detail {
  color: rgba(255, 255, 255, 0.8);
}
#messages .diceroll.prive {
  opacity: 0.5;
}
#messages .diceroll.prive:hover {
  opacity: 1;
}
#messages .diceroll .user {
  font-weight: bold;
}
#messages .diceroll .dice-total {
  display: block;
  font-size: 28px;
  line-height: 36px;
}
.dices {
  overflow: hidden;
  float: right;
  background: #333;
  padding: 10px 10px 5px 10px;
  height: 100%;
}
.dices .dice {
  transition: all 0.5s ease;
}
#dices {
  position: relative;
  max-width: 40px;
}
.dice {
  position: relative;
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin-bottom: 5px;
  text-align: center;
  font-weight: bold;
  color: #fff;
  background: #000;
  cursor: pointer;
  font-size: 10px;
  opacity: 1;
}
.dice.d4 {
  background: url("icons/d4.svg") no-repeat center;
}
.dice.d6 {
  background: url("icons/d6.svg") no-repeat center;
}
.dice.d8 {
  background: url("icons/d8.svg") no-repeat center;
}
.dice.d10 {
  background: url("icons/d10.svg") no-repeat center;
}
.dice.d12 {
  background: url("icons/d12.svg") no-repeat center;
}
.dice.d20 {
  background: url("icons/d20.svg") no-repeat center;
}
.dice.d100 {
  background: url("icons/d10.svg") no-repeat center;
}
.dice:hover {
  color: #2196F3;
}
.dice.cooldown {
  cursor: default;
  opacity: 0.3;
}
.dice.cooldown:hover {
  color: #fff;
}
#yt-player {
  position: absolute;
  top: -1px;
  left: -1px;
}
.music-selector h4 {
  position: relative;
  margin: 0;
  padding-left: 20px;
  line-height: 26px;
}
.music-selector ul {
  position: relative;
  padding-left: 0;
  list-style: none;
}
.music-selector li {
  position: relative;
  line-height: 26px;
  padding-left: 20px;
}
.music-selector .list:hover .icon-play {
  opacity: 1;
}
.music-selector .popin {
  position: static !important;
}
.music-selector .icon-play {
  position: absolute;
  right: 0;
  transition: all 0.6s ease;
  opacity: 0;
}
.music-selector .icon-play .fa {
  font-size: 10px;
  padding: 8px;
  line-height: 10px;
}
.music-selector .volet-btn {
  position: absolute;
  top: 0;
  line-height: 25px;
  width: 20px;
  text-align: center;
  z-index: 1;
}
.music-selector .volet-content {
  display: none;
}
.music-selector .volet-checkbox {
  position: absolute;
  visibility: hidden;
  z-index: -1;
  height: 0;
  width: 0;
}
.music-selector .volet-checkbox:checked + label:before {
  content: "\f0d7";
}
.music-selector .volet-checkbox:checked + label + .volet-content {
  display: block;
}
.popin-close {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 20px;
  line-height: 40px;
  width: 40px;
  text-align: center;
  color: #000;
}
.volume-controls {
  float: left;
  margin: 0 16px 0 8px;
}
#play {
  display: none;
  float: left;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  color: #fff;
}
#play:hover {
  color: #2196F3;
}
#mute {
  width: 34px;
  cursor: pointer;
  color: #fff;
}
#mute:hover {
  color: #2196F3;
}
.audio-controls {
  float: right;
}
.audio-volume {
  position: relative;
  display: block;
  float: right;
  width: 50px;
  height: 36px;
  padding: 15px 0;
  line-height: 30px;
  vertical-align: top;
  cursor: pointer;
}
.audio-volume-empty {
  display: block;
  height: 100%;
  width: 100%;
  background: #666;
}
.audio-volume-bar {
  display: block;
  height: 100%;
  width: 100%;
  background: #2196F3;
}
.admin-layout section.middle {
  padding-top: 90px;
}
.admin-layout #music-player {
  position: fixed;
  top: 60px;
  height: 30px;
  width: 100%;
  background: #666;
  color: #fff;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
}
.admin-layout #play {
  display: block;
}
.admin-layout #options {
  display: inline;
}
.admin-layout .user {
  cursor: -webkit-grab;
  cursor: -moz-grab;
  cursor: -ms-grab;
  cursor: grab;
}
.admin-layout .user.grabbing {
  background: #ddd;
}
.admin-layout.grabbing,
.admin-layout.grabbing * {
  cursor: -webkit-grabbing !important;
  cursor: -moz-grabbing !important;
  cursor: -ms-grabbing !important;
  cursor: grabbing !important;
}
.admin-layout.grabbing #users .user .user-details {
  display: none !important;
}
.recherche {
  width: 100%;
  padding: 5px 10px;
}
.centerer {
  text-align: center;
  white-space: nowrap;
}
.centerer:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  margin-right: -0.25em;
}
.centered {
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  white-space: normal;
  width: 100%;
}
.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.closable {
  cursor: crosshair;
}
.text-align-center {
  text-align: center;
}
@media screen and (max-width: 42em) {
  .overlay .popin {
    position: relative;
    padding: 30px 20px;
  }
  .login-wrapper form .mui-btn {
    width: 50%;
    margin: 0;
  }
}
@media screen and (max-width: 1200px) {
  section.middle aside {
    width: 195px;
  }
  #content {
    margin-left: 195px;
  }
}
@media screen and (max-width: 935px) {
  section.middle aside {
    width: 150px;
  }
  #content {
    margin-left: 150px;
  }
}
@media screen and (max-width: 670px) {
  section.middle {
    padding-bottom: 120px;
  }
  section.middle aside {
    width: 25%;
  }
  .dices {
    position: fixed;
    bottom: 60px;
    width: 100%;
    height: 60px;
    padding: 10px 5px 10px 10px;
    overflow: auto;
  }
  #dices {
    max-height: 40px;
    max-width: none;
  }
  .dice {
    float: left;
    margin-right: 5px;
    margin-bottom: 0;
  }
  #content {
    margin-left: 25%;
    margin-right: 0;
  }
}
